<style>
  .echarts {
    width: 100%;
    height: 100%;
  }
  .title{
    text-align: center;
    font-size:1.5em;
    font-weight: bold;
    margin-bottom: 20px;
  }
</style>

<template>
  <div>
    <div class="title">雅居乐花园"四返"人员信息统计</div>
  <chart ref="region" :options="region"/>
  <chart ref="isolate"  :options="isolate"/>
  <chart ref="line"  :options="line"/>
  </div>
</template>

<script>
  import 'echarts/lib/chart/bar'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title.js'
  import 'echarts/lib/chart/pie'


  export default {
    name:"HelloWorld",
    data () {
      return {
        region: {
          title : {
            text: '"四返"人员出发地信息统计',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter:"{a} <br/>{b} : {c}人 ({d}%)"
          },
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['湖北武汉','两省四市','其他地区']
          },
          series : [
            {
              name: '此地区人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
              ,
              data:[
                {value:90, name:'两省四市'},
                {value:20, name:'湖北武汉'},
                {value:135, name:'其他地区'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        isolate: {
          title : {
            text: '解除隔离人数占比',
            subtext: '动态数据',
            x:'left'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c}人 ({d}%)"
          },
          legend: {
            show: true,
            orient: 'horizontal',
            bottom: '20px',
            data: ['正在隔离人数','解除隔离人数']
          },
          series : [
            {
              name: '此类人员数量',
              type: 'pie',
              radius : '50%',
              center: ['50%', '50%'],
              label: {
                formatter: '{b}：{c}人 '}
                ,
              data:[
                {value:235, name:'正在隔离人数'},
                {value:35, name:'解除隔离人数'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        },
        line:{
          title : {
            text: '隔离人数变化趋势',
            subtext: '动态数据',
            x:'left'
          },
          xAxis: {
            type: 'category',
            data: ['2月1日', '2月2日', '2月3日', '2月4日', '2月5日', '2月6日', '2月7日']
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [200, 180, 174, 153, 133, 121, 110],
            type: 'line'
          }]
        }

      }
    }
  ,
  methods: {
    loadData () {

      var that = this;

      this.$axios.get('https://www.geckos.top/api/peopleinfos')
        .then(res => {
          if (res.data!=null) {

            var peopleInfos = res.data;

            that.region.series[0].data=[
              {value:peopleInfos.hotCity, name:'两省四市'},
              {value:peopleInfos.hubei, name:'湖北武汉'},
              {value:peopleInfos.otherCity, name:'其他地区'}
            ];

            that.isolate.series[0].data=[
              {value:peopleInfos.isolate, name:'正在隔离人数'},
              {value:peopleInfos.notIsolate, name:'解除隔离人数'}
            ];

            that.line.xAxis.data= peopleInfos.dayliyDate;
            that.line.series[0].data=peopleInfos.dayliyIsolateNum;

            //that.$refs.region.mergeOptions(that.region);
          }
        })

    }
  },
  created () {
    this.loadData()
  },
  mounted () {

  }
  }
</script>



